body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.game-info {
  margin-left: 30px;
  text-align: left;
  width: 150px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 72px;
  font-weight: bold;
  line-height: 34px;
  height: 150px;
  width: 150px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

@media (max-width: 640px) {
  .game-info {
    text-align: center;
    margin-left: 0;
  }
}

@media (max-width: 500px) {
  .square {
    height: 100px;
    width: 100px;
    font-size: 60px;
  }
}

@media (max-width: 360px) {
  .square {
    height: 75px;
    width: 75px;
    font-size: 48px;
  }
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
